@mixin list-link-item {
  color: var(--color-text);
  border-bottom: 2px solid var(--color-border);

  &:hover {
    border-bottom-color: var(--color-text-secondary);
  }
}

/* colours */
@mixin highlight {
  color: var(--highlight);
}
@mixin highlight-background {
  background-color: var(--highlight-background);
}